<template>
  <div class="sport-description">
    <!--  -->
    <div v-for="match in sortedMatches" :key="match.type" class="match-container" :class="{
      'active': match.status === 'ongoing',
      'finished': match.status === 'finished',
      'upcoming': match.status === 'upcoming'
    }">
      <!--  -->
      <div class="session-label">
        <span>{{ match.type }}</span>
        <span class="match-status" :class="match.status">
          {{ getStatusText(match.status) }}
        </span>
      </div>

      <!--  -->
      <div class="time-range">
        <div class="time-box start-time">
          <span class="time-label">:</span>
          <span class="time-value">{{ formatDate(match.startTime, 'time') }}</span>
        </div>
        <div class="time-divider">-</div>
        <div class="time-box end-time">
          <span class="time-label">:</span>
          <span class="time-value">{{ formatDate(match.endTime, 'time') }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import request from '@/utils/request';

export default {
  name: 'SportMatchDisplay',

  // props
  props: {
    onMatchChange: {
      type: Function,
      default: () => { }
    },
    spId: {
      type: String,
    }
  },

  data() {
    return {
      matches: [],        // 
      intervalId: null,   // ID
      serverTimeOffset: 0 // 
    }
  },

  mounted() {
    this.fetchScoreData();
    this.intervalId = setInterval(() => {
      this.updateMatchesStatus(); // 5
    }, 5000);
  },

  beforeDestroy() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
    }
  },

  computed: {
    // 
    sortedMatches() {
      const matches = this.matches.map(match => ({
        ...match,
        status: this.getMatchStatus(match)
      }));

      return matches.sort((a, b) => {
        // 
        const statusWeight = {
          'ongoing': 0,
          'upcoming': 1,
          'finished': 2
        };

        // 
        const statusDiff = statusWeight[a.status] - statusWeight[b.status];
        if (statusDiff !== 0) return statusDiff;

        // 
        return a.type - b.type;
      });
    },

    // 
    currentTime() {
      return new Date(Date.now() + this.serverTimeOffset);
    }
  },

  methods: {
    // 
    async fetchScoreData() {
      try {
        const response = await request({
          url: '/saishitong/schedule',
          method: 'get',
          params: { spId: this.spId }
        });

        this.handleResponse(response);
      } catch (error) {
        console.error(':', error.message || '');
      }
    },

    // API
    handleResponse(response) {
      if (response?.code === 1) {
        this.matches = response?.data?.list || [];
        this.updateMatchesStatus(); // 
      } else {
        console.warn('API:', response?.message || '');
      }
    },

    // 
    updateMatchesStatus() {
      if (!this.matches.length) return;

      // 
      this.matches = [...this.matches];
    },

    // 
    getMatchStatus(match) {
      try {
        const now = this.currentTime;
        const startTime = new Date(match.startTime);
        const endTime = new Date(match.endTime);

        if (isNaN(startTime.getTime()) || isNaN(endTime.getTime())) {
          return 'upcoming';
        }

        if (now < startTime) return 'upcoming';
        if (now > endTime) return 'finished';
        return 'ongoing';
      } catch (e) {
        console.error(':', e);
        return 'upcoming';
      }
    },

    // 
    getStatusText(status) {
      const statusMap = {
        'ongoing': '',
        'upcoming': '',
        'finished': ''
      };
      return statusMap[status] || '';
    },

    // 
    formatDate(utcString, formatType = 'both') {
      if (!utcString) {
        return formatType === 'both' ? '--/-- --:--' :
          formatType === 'date' ? '--/--' : '--:--';
      }

      // 
      let dateObj;
      try {
        dateObj = new Date(utcString);
      } catch (e) {
        console.error(':', utcString);
        return '--:--';
      }

      // 
      if (!dateObj || isNaN(dateObj.getTime())) {
        return this.extractTimeFromString(utcString, formatType);
      }

      // 
      return this.formatDateComponents(dateObj, formatType);
    },

    // 
    extractTimeFromString(utcString, formatType) {
      try {
        if (utcString.length >= 19) {
          const datePart = utcString.substr(5, 5).replace('-', '/'); // MM/DD
          const timePart = utcString.substr(11, 5); // HH:mm

          if (formatType === 'date') return datePart;
          if (formatType === 'time') return timePart;
          return `${datePart} ${timePart}`;
        }
        return formatType === 'date' ? '--/--' : '--:--';
      } catch (error) {
        return formatType === 'date' ? '--/--' : '--:--';
      }
    },

    // 
    formatDateComponents(date, formatType) {
      const padZero = num => num.toString().padStart(2, '0');

      const month = padZero(date.getMonth() + 1);
      const day = padZero(date.getDate());
      const hours = padZero(date.getHours());
      const minutes = padZero(date.getMinutes());

      if (formatType === 'date') return `${month}/${day}`;
      if (formatType === 'time') return `${hours}:${minutes}`;
      return `${month}/${day} ${hours}:${minutes}`;
    }
  }
};
</script>

<style scoped>
.sport-description {
  background: #ffffff;
  color: #333;
  font-family: 'Segoe UI', system-ui, sans-serif;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.match-container {
  display: flex;
  align-items: center;
  padding: 6px 12px;
  border-radius: 4px;
  gap: 12px;
  height: 32px;
}

.session-label {
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 140px;
}

.session-label span:first-child {
  font-size: 0.9rem;
  font-weight: 600;
  color: #2d8cf0;
}

.time-range {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 8px;
  color: #666;
  font-size: 0.85rem;
}

.time-box {
  display: flex;
  align-items: center;
  gap: 4px;
}

.time-label {
  color: #999;
  font-size: 0.85rem;
}

.time-value,
.date-value {
  display: inline;
  color: #666;
  font-size: 0.85rem;
}

.time-divider {
  color: #ccc;
  margin: 0 4px;
  font-size: 0.85rem;
}

.time-divider {
  padding: 0 15px;
  font-weight: 600;
  opacity: 0.6;
  color: #999;
  align-self: center;
}

.match-container {
  border: 1px solid #e8e8e8;
  transition: all 0.3s ease;
}

.match-container.active {
  border-color: #1890ff;
  background-color: rgba(24, 144, 255, 0.05);
}

.match-container.finished {
  border-color: #d9d9d9;
  background-color: rgba(0, 0, 0, 0.02);
}

.match-container.upcoming {
  border-color: #52c41a;
  background-color: rgba(82, 196, 26, 0.05);
}

.match-status {
  padding: 2px 6px;
  border-radius: 3px;
  font-size: 0.75rem;
  line-height: 1.2;
}

.match-status.ongoing {
  background-color: #1890ff;
  color: white;
}

.match-status.finished {
  background-color: #d9d9d9;
  color: rgba(0, 0, 0, 0.65);
}

.match-status.upcoming {
  background-color: #52c41a;
  color: white;
}

/*  */
@media (max-width: 768px) {
  .session-label {
    font-size: 1.4rem;
  }

  .time-value {
    font-size: 1.2rem;
  }

  .date-value {
    font-size: 0.8rem;
  }

  .no-active-match {
    font-size: 1rem;
  }
}
</style>