<template>
    <div class="flv-player-container">
        <video ref="videoElement" class="flv-video" :style="videoStyle" controls autoplay muted playsinline
            @play="handlePlay" @pause="handlePause" @error="handleVideoError"></video>
        <div v-if="reconnecting" class="reconnect-mask">
            <div class="reconnect-message">
                <i class="el-icon-loading"></i>
                <span>... ({{ reconnectCount }})</span>
            </div>
        </div>
    </div>
</template>

<script>
import flvjs from 'flv.js'

export default {
    name: 'FlvPlayer',
    props: {
        streamUrl: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            flvPlayer: null,
            playing: false,
            reconnecting: false,
            reconnectCount: 0,
            maxReconnectAttempts: 999999,
            reconnectTimer: null,
            videoWidth: 0,
            videoHeight: 0,
            //  AbortError 
            pendingPlay: false,
            lastPlayTime: 0
        }
    },
    computed: {
        videoStyle() {
            return {
                width: '100%',
                height: '100%',
                objectFit: 'contain',
                backgroundColor: '#000'
            }
        }
    },
    watch: {
        streamUrl(newUrl) {
            if (newUrl) {
                this.reconnectCount = 0
                this.destroyPlayer()
                this.initPlayer(newUrl)
            }
        }
    },
    mounted() {
        this.initPlayer(this.streamUrl)
    },
    beforeDestroy() {
        this.destroyPlayer()
    },
    methods: {
        initPlayer(url) {
            if (!url) return

            // 
            this.destroyPlayer()

            if (flvjs.isSupported()) {
                const videoElement = this.$refs.videoElement
                if (!videoElement) return

                try {
                    this.flvPlayer = flvjs.createPlayer({
                        type: 'flv',
                        url: url,
                        isLive: true,
                        hasAudio: false,
                        stashInitialSize: 128,
                        enableWorker: true,
                        enableStashBuffer: true
                    })

                    this.flvPlayer.attachMediaElement(videoElement)
                    this.flvPlayer.load()

                    // 
                    this.flvPlayer.on(flvjs.Events.MEDIA_INFO, (mediaInfo) => {
                        this.videoWidth = mediaInfo.width
                        this.videoHeight = mediaInfo.height
                        this.$emit('resolution-change', {
                            width: mediaInfo.width,
                            height: mediaInfo.height
                        })
                    })

                    // 
                    this.flvPlayer.on(flvjs.Events.ERROR, (errType, errDetail) => {
                        console.error('FLV:', errType, errDetail)
                        this.handlePlayerError()
                    })

                    // 
                    this.safePlay().catch(err => {
                        console.warn(':', err)
                        this.handlePlayerError()
                    })

                } catch (err) {
                    console.error('FLV:', err)
                    this.handlePlayerError()
                }
            } else {
                console.error('flv.js')
                this.$emit('error', new Error('flv.js'))
            }
        },

        async safePlay() {
            //  play()  AbortError
            if (this.pendingPlay) return
            if (Date.now() - this.lastPlayTime < 1000) return

            this.pendingPlay = true
            this.lastPlayTime = Date.now()

            try {
                const videoElement = this.$refs.videoElement
                if (videoElement) {
                    await videoElement.play()
                    this.playing = true
                }
            } catch (err) {
                if (err.name === 'AbortError') {
                    console.warn('')
                } else {
                    console.error(':', err)
                    throw err
                }
            } finally {
                this.pendingPlay = false
            }
        },

        pause() {
            const videoElement = this.$refs.videoElement
            if (videoElement) {
                videoElement.pause()
                this.playing = false
            }
        },

        handlePlay() {
            this.playing = true
        },

        handlePause() {
            this.playing = false
        },

        handleVideoError() {
            const videoElement = this.$refs.videoElement
            if (videoElement) {
                console.error(':', videoElement.error)
            }
            this.handlePlayerError()
        },

        handlePlayerError() {
            if (this.reconnecting) return

            this.reconnecting = true
            this.reconnectCount++

            if (this.reconnectCount > this.maxReconnectAttempts) {
                this.$emit('error', new Error(` (${this.maxReconnectAttempts})`))
                this.reconnecting = false
                return
            }

            this.destroyPlayer()

            // 
            clearTimeout(this.reconnectTimer)
            this.reconnectTimer = setTimeout(() => {
                if (this.streamUrl) {
                    this.initPlayer(this.streamUrl)
                }
                this.reconnecting = false
            }, 3000)
        },

        destroyPlayer() {
            if (this.flvPlayer) {
                try {
                    this.flvPlayer.pause()
                    this.flvPlayer.unload()
                    this.flvPlayer.detachMediaElement()
                    this.flvPlayer.destroy()
                } catch (err) {
                    console.warn(':', err)
                }
                this.flvPlayer = null
            }
            this.playing = false
            clearTimeout(this.reconnectTimer)
        }
    }
}
</script>

<style scoped>
.flv-player-container {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #000;
}

.flv-video {
    display: block;
    width: 100%;
    height: 100%;
}

.reconnect-mask {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
}

.reconnect-message {
    color: #fff;
    font-size: 16px;
    padding: 20px;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 4px;
    display: flex;
    align-items: center;
}

.reconnect-message i {
    margin-right: 10px;
    font-size: 20px;
}
</style>